/*menu panel 1*/


section.page-header div.menu-panel ul:nth-child(1) {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

section.page-header div.menu-panel ul:nth-child(1) li:not(#list-panel) {
    display: block;
    padding: 0;
    margin: 0;
    float: left;
    height: auto;
}
.course-list-parent {
    float: left;
    display: block;
    height: auto;
    padding: 0;
    margin: 0;
    background-color: rgb(63, 60, 60)(63, 60, 60);
    padding: 10px 16px;
    cursor: pointer;
    font-size: 14px;



}

.course-list-panel {
    transition:   2s ;
    display: none;
    position: absolute;
    list-style-type: none;
    background:   #2e4053 ;
    opacity: 0.9;


    padding: 10px;
    margin: 0;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
    color: white;
}

.course-list-parent:hover .course-list-panel {
    display: block;
}

.course-list-panel::before {
    content: " ";
    position: absolute;
    border-width: 10px;
    border-style:  solid;
    border-color: transparent transparent  #2e4053  transparent;
    margin-top: -26px;



}




#list-panel:not(.course-list-parent){
    display: block;
    padding: 0;
    margin: 0;
    height: auto;
}


#list-panel-anchor {
    display: block;
    padding: 10px 16px;
    margin: 0;
    /* color: #1c2833; */
