      background:    #34495e;
      position: fixed;
      z-index: 1;
      border-radius: 15px;
      margin: 10px;
      display: none;
    }
    div.small-header-menu {
      height: 100%;
    }

    .search {
      border: 1px solid white;
      border-radius: 5px;
      width: 100%;
    }

    div.small-header-menu ul {
      width: 100%;
      padding: 0;
      margin: 0;
    }

    div.small-header-menu ul li {
      list-style-type: none;
      display: block;
      padding: 0;
      margin: 0;
    }
    div.small-header-menu ul li a{
      text-decoration: none;
      display: block;
      padding: 10px 20px;
      margin: 0;
      color: white;
    }

    div.left-feed-panel {
      display: none;
    }

    div.main-feed-panel {
      width: 100%;
      margin-left: 0;
      padding: 0;
    }
    #toggleMenu {
      display: none;
    }

    .card-info {
        float: left;
        padding: 2px;
        margin-left: 2px;
        color:  #566573 ;
        background-color:  #d5d8dc ;
        border-radius: 5px;
        font-size: 0.6em;
    }

    .card-info-time {
        float: left;
        padding: 2px;
        margin-left: 2px;
        color:  white ;
        background-color:   #d81b60  ;
        border-radius: 5px;
        font-size: 0.6em;
    }

    div.comment-card {
      display: none;
      height:  400px;
      width: 90%;
      overflow-y: scroll;
      background-color:  #880e4f;
      position: fixed;
      z-index: 1;
      border-radius: 10px;
      margin: auto;
      flex-direction: column;
      padding: 10px;
      box-shadow:
      0 2.8px 2.2px rgba(0, 0, 0, 0.034),
      0 6.7px 5.3px rgba(0, 0, 0, 0.048),
      0 12.5px 10px rgba(0, 0, 0, 0.06),
      0 22.3px 17.9px rgba(0, 0, 0, 0.072),
      0 41.8px 33.4px rgba(0, 0, 0, 0.086),
      0 100px 80px rgba(0, 0, 0, 0.12);
      /* opacity: 0.9; */
      left: 20px;
      top: 100px;

    }

